.ArcTable-Cell  {
  padding: var(--padding);
  box-sizing: border-box;
}

  .ArcTable-Cell.highlighted {
    background: var(--cell-highlight-background);
  }

  .ArcTable-Cell.hover {
    background: var(--cell-hover-background);
  }

  .ArcTable-Cell:not(:last-of-type) {
    border-right: var(--border);
  }
