.ArcEdge {
  stroke-width: 3px;
  cursor: pointer;
}

  .ArcEdge path {
    stroke: transparent;
    fill: var(--grey-4);
    opacity: 0.7;
  }

  .ArcEdge polygon {
    fill: var(--secondary);
    stroke: var(--grey-5);
    stroke-width: 1px;
  }

  .ArcEdge:hover path {
    stroke: var(--primary);
  }
