.ArcAccordion {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  transition: height 300ms;
}

  .ArcAccordion .item {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid var(--color-border);
    transition: height 300ms;
  }

  .ArcAccordion .item .header {
      flex: 0 0 auto;
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 8px 12px 8px 0;
      cursor: pointer;
    }

  .ArcAccordion .item .header .label {
        flex: 1 1;
      }

  .ArcAccordion .item .header .ArcAccordion-Button {
        flex: 0 0 24px;
        width: 24px;
        height: 24px;
        padding: 0;
        margin-right: 8px;

        display: flex;
        justify-content: center;
        align-items: center;

        border-radius: 100%;

        background: white;
        border: 2px solid var(--grey-2);
        transform: rotate(0deg);
        transition: 300ms;
        cursor: s-resize;
      }

  .ArcAccordion .item .header .ArcAccordion-Button .X line {
          stroke: var(--grey-2);
          stroke-width: 2px;
        }

  .ArcAccordion .item .header .ArcAccordion-Button.expanded {
          cursor: n-resize;
          transform: rotate(45deg);
        }

  .ArcAccordion .item .children {
      overflow: hidden;
      opacity: 1;
      padding: 0 2px;
      transition: height 300ms, opacity 150ms;
      transition-delay: 0, 150ms;
    }

  .ArcAccordion .item .children.collapsed {
        opacity: 0;
        transition: height 300ms, opacity 150ms;
        transition-delay: 0, 0;
      }
